.jelly-full {
  background: rgba(50, 135, 214, 0.8);
  width:100%;
  height: 90%;
  min-height: 100vh;
  justify-content: center;
  border-radius: 20px;
  display: flex;
  align-items: center;
}
body {
  margin: 0;
  padding: 0;
  background: #131731;
  overflow: hidden;
}

.cube {
  position: absolute;
  top: 38%;
  right: 2%;
  width: 100px;
  height: 100px;
  transform-style: preserve-3d;
  transform: translate(-100%, -100%);
}

.cube span {
  position: absolute;
  top: 0;
  display: block;
  width: 100%;
  height: 100%;
  background: #ff0;
  transform-style: preserve-3d;
}

.cube span::before {
  content: "";
  position: absolute;
  width: 75px;
  height: 75px;
  background: #fff;
}

.cube span::after {
  content: "";
  position: absolute;
  width: 50px;
  height: 50px;
  background: #fff;
  transition: 1s;
  opacity: 0;
}

.cube:hover span::after {
  opacity: 0.2;
}

.cube span:nth-child(1) {
  left: 50%;
  transform: rotate(-45deg) skew(15deg, 15deg);
  background: #3264ff80
}

.cube span:nth-child(1)::before {
  bottom: 0;
  left: 0;
  background: rgba(50, 135, 214, 0.5);
}

.cube span:nth-child(2) {
  top: 77.5px;
  left: 5px;
  transform: rotate(15deg) skew(15deg, 15deg);
  background: rgba(50, 124, 211, 0.5);
}

.cube span:nth-child(2)::before {
  top: 0;
  right: 0;
  background: rgba(50, 123, 211, 0.5);
}


.cube span:nth-child(3) {
  top: 77.5px;
  left: 94.5px;
  transform: rotate(-15deg) skew(-15deg, -15deg);
  background: rgba(50, 111, 188, 0.5);
}

.cube span:nth-child(3)::before {
  top: 0;
  left: 0;
  background: rgba(50, 111, 188, 0.5);
}


